<script>
	export default{
		data(){
			return{
				hide:"hide",
				show:true,
				appear:false,
				newsDataFirstScreen:[],
				newsDataSecondScreen:[],
				sideBar:false
			}
		},
		methods:{
			reClass(){
				this.hide = "";
				this.show = !this.show;
			},
			change(){
				this.appear = !this.appear
			},
			backTop(){
				document.documentElement.scrollTop = 0;
			}
		},
		mounted(){
			var _this = this;
			//AJAX
			this.$http.get('./data/articleData.json')
			.then((res)=>{
				this.newsDataFirstScreen = res.data.newsDataFirstScreen;
				this.newsDataSecondScreen = res.data.newsDataSecondScreen;
				
				console.log(res.data.newsDataFirstScreen);
			})
			.catch(()=>{})
			.finally((f)=>{
				console.log('')
		})
			
			//
			document.onscroll = function(){
//				console.log(document.documentElement.scrollTop)
				if(document.documentElement.scrollTop>300){
					
					_this.sideBar=true;
					
				}else{
					_this.sideBar = false;
				}
//				console.log(_this.backtopsituat)
			}
	}
}
</script>

