		margin-top: 10px;
		margin-bottom: 15px;
	}
	.webMapBox{
		width: 100%;
		overflow: hidden;
	}
	.webMapHeader{
		width: 100%;
		overflow: hidden;
		padding-bottom: 15px;
		padding-top: 25px;
	}
	.webMapHeader h4{
		color: #3c3c3d;
		font-size: 17.5px;
		float: left;
		margin-left: 3%;
		margin-top: 5px;
	}
	.webMapBox ul{
		width: 100%;
		overflow: hidden;
		padding-bottom: 15px;
	}
	.webMapBox ul li{
		float: left;
		text-align: center;
		width: calc(100%/6);
		font-size: 1.3em;
		margin-bottom: 30px;
		color: #3C3C3D;
	}
	footer{
		text-align: center;
		width: 100%;
		background-color: #F5F5F5;
		padding-top: 40px;
		padding-bottom: 30px;
	}
	.sugguestAcoorperationText{
		font-size: 14px;
		color: #3C3C3D;
		margin-bottom: 30px;
	}
	.footerText{
		color: #A4A4A4;
		font-size: 12px;		
	}
	
	/*******************************/
	.sidebar{
		width: 9.71%;
		max-width: 52.39px;
		position: fixed;
		right: 15px;
		bottom: 90px;
	}
	.sidebar>li{
		display: block;
		width: 100%;
		padding-bottom: 100%;
		border: 1px solid #fce4af;
		margin-top: 5px;
		border-radius: 4px;
		background-color: rgba(255,244,220,.95);
		position: relative;
		overflow: hidden;
	}
	.sidebar>li.backTop>span{
		position: absolute;
		top: calc(50% - 15px);
		left: calc(50% - 13px);
		display: inline-block;
		width: 27px;
		height: 40px;
		background: url(../../assets/articleImg/backTop.png) no-repeat;
		background-size: contain;
	}
	.sidebar>li.backIndex span{
		position: absolute;
		top: calc(50% - 20px);
		left: calc(50% - 20px);
		display: inline-block;
		width: 40px;
		height: 40px;
		background: url(../../assets/articleImg/indexBtn.png) no-repeat;
		background-size: contain;
	}
	.sidebar>li.download{
		background-color: #fbb724;
	}
	.sidebar>li.download>span{
		position: absolute;
		top: calc(50% - 18px);
		left: calc(50% - 15px);
		display: inline-block;
		width: 40px;
		font-size: 14px;
		color: white;
	}
	/*************************************/
	.footerBar{
		position: fixed;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		height: 11.3vw;
		max-height: 59px;
		background-color: #F0F0F0;
		overflow: hidden;
		box-sizing: border-box;
		padding-left: 15px;
		padding-right: 15px;
		max-width: 780px;
	}
	.footerBar>.headerIcon{
		float:left;
		width: 8.5vw;
		height: 8vw;
		max-width: 45.19px;
		max-height: 45.19px;
		border-radius: 50%;
		background: url(../../assets/articleImg/headerPic.gif);
		background-size: contain;
		margin-top: 6px;
	}
	.footerBar>input{
		width: 65%;
		margin-left: 4%;
		outline-style: none;
		font-size: 15px;
		padding: 1vw;
		margin-top: 2vh;
		border-radius: 5px;
	}
	.footerBar>.commentBtn{
		float: right;
		margin-right: 3%;
		width: 5.33vw;
		height: 5.33vw;
		background: url(../../assets/articleImg/commentBtn.png) no-repeat;
		background-size: contain;
		max-width: 28.8px;
		max-height: 28.8px;
		margin-top: 15px;
		opacity: 0.7;
	}
	.footerBar>.shareBtn{
		float: right;
		width: 5.33vw;
		height: 5.33vw;
		background: url(../../assets/articleImg/shareBtn.png) no-repeat;
		background-size: contain;
		max-width: 28.8px;
		max-height: 28.8px;
		opacity: 0.5;
		margin-top: 14px;
		margin-right: 1%;
	}
</style>


